import Vue from 'vue';
import { Card, Form, FormItem, Row, Col, Input, Select, Option, InputNumber, DatePicker, Message, Dialog, Dropdown, DropdownItem, DropdownMenu } from 'element-ui';
import * as moment from 'moment';
import { Badge } from 'vue-antd-ui';
import PageHeaderLayout from 'layouts/PageHeaderLayout/index.vue';
import AntIcon from 'components/AntIcon';
import StandardTable from 'components/StandardTable';
import Divider from 'components/Divider';
Vue.use(Card);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Row);
Vue.use(Col);
Vue.use(Input);
Vue.use(Select);
Vue.use(Option);
Vue.use(InputNumber);
Vue.use(DatePicker);
Vue.use(Dialog);
Vue.use(Dropdown);
Vue.use(DropdownItem);
Vue.use(DropdownMenu);
Vue.use(AntIcon);
Vue.use(StandardTable);
Vue.use(Divider);
Vue.component(Badge.name, Badge);
const getValue = (obj) => Object.keys(obj)
    .map(key => obj[key])
    .join(',');
const statusMap = ['default', 'processing', 'success', 'error'];
const status = ['', '', '', ''];
export default Vue.extend({
    components: {
        PageHeaderLayout
    },
    data() {
        const columns = [
            {
                prop: 'no',
                label: ''
            },
            {
                prop: 'description',
                label: ''
            },
            {
                prop: 'callNo',
                label: '',
                align: 'right',
                sortable: true,
                scopedSlot: 'callNo'
            },
            {
                prop: 'status',
                label: '',
                scopedSlot: 'status',
                columnKey: 'status',
                filters: [
                    {
                        text: status[0],
                        value: 0
                    },
                    {
                        text: status[1],
                        value: 1
                    },
                    {
                        text: status[2],
                        value: 2
                    },
                    {
                        text: status[3],
                        value: 3
                    }
                ],
                filterMethod: (value, row, column) => {
                    return row.status.toString() === value.toString();
                }
            },
            {
                prop: 'updatedAt',
                label: '',
                sortable: true,
                scopedSlot: 'updatedAt'
            },
            {
                label: '',
                scopedSlot: 'operation'
            }
        ];
        const selectedRows = [];
        return {
            modalVisible: false,
            expandForm: false,
            form: {
                no: '',
                status: '',
                number: '',
                updatedAt: '',
                status3: '',
                status4: ''
            },
            selectedRows,
            loading: false,
            columns: columns,
            status,
            statusMap,
            addForm: {
                desc: ''
            },
            addFormRules: {
                desc: [{ required: true, message: 'Please input some description...' }]
            }
        };
    },
    computed: {
        rule() {
            return this.$store.state.rule;
        }
    },
    mounted() {
        this.$store.dispatch('rule/fetch');
    },
    methods: {
        handleStandardTableChange(pagination, filtersArg, sorter) {
            const filters = Object.keys(filtersArg).reduce((obj, key) => {
                const newObj = Object.assign({}, obj);
                newObj[key] = getValue(filtersArg[key]);
                return newObj;
            }, {});
            const params = Object.assign({ currentPage: pagination.currentPage, pageSize: pagination.pageSize }, this.form, filters);
            if (sorter.prop) {
                params.sorter = `${sorter.prop}_${sorter.order}`;
            }
            this.$store.dispatch('rule/fetch', params);
        },
        handleFormReset() {
            const formRef = this.$refs.form;
            formRef.resetFields();
            this.$store.dispatch('rule/fetch', {});
        },
        toggleForm() {
            this.expandForm = !this.expandForm;
        },
        handleMenuClick(key) {
            const { selectedRows } = this;
            if (!selectedRows) {
                return;
            }
            switch (key) {
                case 'remove':
                    this.$store
                        .dispatch('rule/remove', {
                        no: selectedRows.map(row => row.no).join(',')
                    })
                        .then(() => {
                        this.selectedRows = [];
                    });
                    break;
                default:
                    break;
            }
        },
        handleSelectRows(rows) {
            this.selectedRows = rows;
        },
        handleSearch(e) {
            e.preventDefault();
            const formRef = this.$refs.form;
            formRef.validate((valid) => {
                if (valid) {
                    this.loading = true;
                    const vaules = Object.assign({}, this.form, { updatedAt: this.form.updatedAt && this.form.updatedAt.valueOf() });
                    this.$store
                        .dispatch('rule/fetch', vaules)
                        .then(() => {
                        this.loading = false;
                    })
                        .catch(() => {
                        this.loading = false;
                    });
                }
            });
        },
        handleModalVisible(flag) {
            this.modalVisible = !!flag;
        },
        handleModalOk() {
            const formRef = this.$refs.addForm;
            formRef.validate((valid) => {
                if (valid) {
                    this.handleAdd(this.addForm);
                    formRef.resetFields();
                }
            });
        },
        handleAdd(fields) {
            this.$store.dispatch('rule/add', {
                description: fields.desc
            });
            Message.success('');
            this.modalVisible = false;
        },
        moment(value) {
            return moment(value);
        }
    }
});



